.input{
  box-sizing: border-box;
  width: 100%;
  border: 2px solid black;
  border-radius: 5px;
  padding: 5px 10px;
  box-shadow: 2px 2px 5px 3px var(--color-black-shadow);
  &:focus {
    box-shadow: 2px 2px 5px 3px var(--color-blue-shadow);
  }
}
.label {
  font-size: 17px;
  padding: 7px 0;
  display: inline-block;
}
