.container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 10px;
  .body {
    min-width: 270px;
    .title {
      display: flex;
      justify-content: center;
      gap: 10px;
      align-items: center;
      padding: 10px 5px;
      .line {
        font-size: 25px;
        font-weight: 900;
      }
    }
  }
}
